//
// Glyphicons in Attribute Modules - borrowed from Bootstrap
//
// Since icons are fonts, they can be placed anywhere text is placed and are
// thus automatically sized to match the surrounding child. To use, create an
// inline element with the appropriate classes, like so:
//
// Bootstrap does it like this:
// <a href="#"><span class="glyphicon glyphicon-star"></span> Star</a>
// 
// We do it like this:
// <a href="#"><span am-Icon="glyph" class="star"></span> Star</a>

// Import the fonts
@font-face {
  font-family: 'Glyphicons Halflings';
  src: url('@{icon-font-path}@{icon-font-name}.eot');
  src: url('@{icon-font-path}@{icon-font-name}.eot?#iefix') format('embedded-opentype'),
       url('@{icon-font-path}@{icon-font-name}.woff') format('woff'),
       url('@{icon-font-path}@{icon-font-name}.ttf') format('truetype'),
       url('@{icon-font-path}@{icon-font-name}.svg#@{icon-font-svg-id}') format('svg');
}

// Catchall baseclass
[am-Icon~=glyph] {
  position: relative;
  // top: 1px;
  display: inline-block;
  font-family: 'Glyphicons Halflings';
  font-style: normal;
  font-weight: normal;
  // line-height: 1;
  margin:0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[am-Icon~=glyph][am-Icon~=nav-item] {
  top:5px;
}

[am-Icon~=glyph][am-Icon~=extra-small] {
  font-size: 0.6em;
}

[am-Icon~=glyph][am-Icon~=small] {
  font-size: 0.8em;
}

[am-Icon~=glyph][am-Icon~=normal] {
  font-size: 1em;
}

[am-Icon~=glyph][am-Icon~=large] {
  font-size: 1.14em;
}

[am-Icon~=glyph][am-Icon~=extra-large] {
  font-size: 1.3em;
}

